@import './src/styles/variables';
@import './src/styles/mixins';

.forCommunication {
  display: flex;
  justify-content: center;
  padding: 40px 0;

  .forCommunicationWrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    max-width: 1440px;
    background: $backgroundGradient;
    box-shadow: $boxShadow;
    border-radius: 15px;
    padding: 20px;

    .title {
      @include sectionTitle();
    }

    .feedBack {
      width: 50%;
      min-width: 340px;

      .form {
        margin: 0 20px;

        .formExample {
          position: relative;
          display: flex;
          justify-content: center;
          box-shadow: $boxShadow;
          height: 45px;
          margin: 30px 0;
          border-radius: 15px;

          .label {
            text-align: center;
            position: absolute;
            top: -22px;
            left: 20px;
            font-size: 16px;
            letter-spacing: 2px;
            background-color: transparent;
          }

          input {
            width: 90%;
            height: 95%;
            font-size: 20px;
            font-weight: 500;
            letter-spacing: 2.2px;
            text-align: center;
            border: none;
            outline: none;
            background-color: transparent;
          }


        }
      }

      .yourText {
        display: flex;
        justify-content: center;
        align-items: center;
        box-shadow: $boxShadow;
        border-radius: 15px;

        textarea {
          padding: 5px;
          width: 100%;
          height: 100px;
          font-size: 20px;
          letter-spacing: 1.9px;
          border: none;
          background: transparent;
          resize: none;
          overflow: auto;
          outline: none;
          text-align: center;
        }
      }
    }

    .submit {
      margin: 10px 0;
      width: 315px;
      min-width: 200px;
      height: 40px;
      font-size: 20px;
      letter-spacing: 4px;
      background-color: transparent;
      border: none;
      border-radius: 25px;
      box-shadow: $boxShadow;
    }
  }
}